<template>
  <div class="mapye">
    <!-- <div id="mapbox"></div> -->
    <div id="allmap" ref="allmap"></div>
    <div class="nav">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <img src="./img/gengduo_1@2x.png" alt />
    </div>
    <div class="fudong">
      <img src="./img/dingwei_1@2x.png" class="left" />
      <img src="./img/sousuo_1@2x.png" class="right" />
      <p class="left"></p>
    </div>
  </div>
</template>

<script>

export default {
  props: {},
  data() {
    return {};
  },
  mounted() {
      this.map();
  },
  methods: {
     map(){
      let map =new BMap.Map(this.$refs.allmap);// Map
      map.centerAndZoom(new BMap.Point(113.64964385, 34.75661006), 11);// ,
      map.addControl(new BMap.MapTypeControl({//
        mapTypes:[
          BMAP_NORMAL_MAP,
          BMAP_HYBRID_MAP
        ]}));
      map.setCurrentCity("");//  
      map.enableScrollWheelZoom(true);//
  },
  // components: { }
  }
}
</script>

<style scoped lang="less">
.mapye {
  padding-top: 1.56rem;
  .nav {
    position: relative;
    z-index: 2;
    height: 0.88rem;
    width: 6.9rem;
    margin: 0.24rem auto 0;
    font-size: 0.28rem;
    display: flex;
    align-items: center;
    background-color: #fff;
    ul {
      display: flex;
      width: 6.3rem;
      height: 100%;
      align-items: center;
      justify-content: space-around;
    }
    img {
      width: 0.36rem;
      height: 0.37rem;
    }
  }
  .fudong {
    position: fixed;
    z-index: 2;
    bottom: 0.4rem;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .left {
      width: 0.6rem;
      height: 0.6rem;
      margin-left: 0.26rem;
    }
    .right {
      width: 1.92rem;
      height: 1.92rem;
    }
  }
  #allmap {
    width: 100%;
    min-height: 13.32rem;
    max-height: 16rem;
    background-color: aqua;
    position: absolute;
    top: 0;
    left: 0;
  }
}
</style>



